export default class BasicLayout {
  constructor() {
    this.a = true;
  }

  renderLayout() {
    this.wrapperDiv = document.createElement('div');
    this.wrapperDiv.setAttribute('class', 'wrapper-main');
    this.wrapperDiv.setAttribute('id', 'mainContent');
    this.wrapperClipListDiv = document.createElement('div');
    this.wrapperClipListDiv.setAttribute('class', 'wrapper-clip-list');
    this.wrapperClipListDiv.setAttribute('id', 'ClipList');
    this.navDiv = document.createElement('div');
    this.navDiv.setAttribute('class', 'navigation');
    this.navDiv.setAttribute('id', 'navigationList');
    this.wrapperSearchBoxDiv = document.createElement('div');
    this.wrapperSearchBoxDiv.setAttribute('class', 'wrapper-search-box');
    this.wrapperSearchBoxDiv.setAttribute('id', 'wrapperSearchBox');

    document.body.appendChild(this.wrapperDiv);
    document.getElementById('mainContent').appendChild(this.wrapperSearchBoxDiv);
    document.getElementById('mainContent').appendChild(this.wrapperClipListDiv);
    document.getElementById('mainContent').appendChild(this.navDiv);
  }

  renderSearchBox() {
    this.input = document.createElement('input');
    this.input.type = 'text';
    this.input.className = '';
    this.searchBox = `
        <div class='wrapper-search'>
             <input class="search-line" id="searchLine" type="text">
             <button class="search-button" id="searchButton">Search</button>
        </div>
    `;
    document.getElementById('wrapperSearchBox').insertAdjacentHTML('afterbegin', this.searchBox);
  }

  renderNavButtons() {
    this.buttonNav = `
          <button class="button-nav prev hiddenBth" id="prevButton">
                    &nbsp;<span class="tooltip"></span>
          </button>
          <button class="button-nav current hiddenBth" id="currentButton">
                    &nbsp;<span class="tooltip"></span>
          </button>
          <button class="button-nav next hiddenBth" id="nextButton">
                    &nbsp;<span class="tooltip"></span>
          </button>
        `;
    document.getElementById('navigationList').insertAdjacentHTML('afterbegin', this.buttonNav);
  }
}
