import BasicLayout from '../views/basicLayoutView/bunchLayout';

export default class BasicLayoutController {
  constructor() {
    this.state = true;
  }

  listenerofLoad() {
    document.addEventListener('DOMContentLoaded', () => {
      this.render = new BasicLayout();
      this.render.renderLayout();
      this.render.renderNavButtons();
      this.render.renderSearchBox();
      this.render.slider();
  
    });
  }
}
