export default class ClipRender {
  constructor() {
    this.k = true;
  }

  renderVideo(ClipId, Clipname, ClipChannelTitle,
    ClipDate, ClipViewCount, ClipDescription, ClipScreen) {
    const fullDate = new Date(ClipDate);
    const data = `${fullDate.getUTCDate()}-${fullDate.getMonth()}-${fullDate.getUTCFullYear()}`;
    this.item = `
            <div class='youtube-clip'>
                  <div class="youtube-clip-top" style='background: url(&quot;${ClipScreen}&quot;);'>
                       <div class='title-youtube-clip' '>
                          <a class='clip-link' 
                          href='https://www.youtube.com/watch?v=${ClipId}' target='_blank'>
                            ${Clipname}
                          </a>
                       </div>
                  </div>
                  <div class="clip-description">
                    <p class="clip-cannel-title"> ${ClipChannelTitle}<span>
                    </span></p>
                    <p class="clip-date"> ${data}<span>
                    </span></p> 
                    <p class="clip-view-count"> ${ClipViewCount}<span>
                    </span></p>
                    <div class="clip-description-content">
                          ${ClipDescription}
                    </div>
                  </div>
            </div>  
         `;

    document.getElementById('ClipList').insertAdjacentHTML('afterbegin', this.item);
  }
}
