export default class ClipModel {
  constructor(state) {
    this.state = state;
  }

  async getOptions() {
    this.result = fetch(this.state)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error, status  =  ${response.status}`);
        }
        return response.json();
      })
      .catch((error) => {
        console.log(error);
      });

    return this.result;
  }

  async getOptionsWithStatistics(uri) {
    this.responce = await fetch(uri);
    this.data = await this.responce.json();
    return this.data;
  }
}
