import ClipModel from '../models/clipModel';
import ClipView from '../views/clipView/bundleClip';

export default class ClipController {
  constructor(state) {
    this.state = state;
  }

  handlerOfOptions(state) {
    console.log(state);
    this.model = new ClipModel(state);
    const res = this.model.getOptions()
      .then((json) => {
        const nextToken = [];
        nextToken.push(json.nextPageToken);
        const videoItems = [];
        for (let i = 0; i < json.items.length; i++) {
          if (json.items[i].id.videoId !== undefined) {
            videoItems.push(json.items[i].id.videoId);
          }
        }
        nextToken.push(videoItems);
        return nextToken;
      });

    return res;
  }

  async handle() {
    this.uri = await this.handlerOfOptions(this.state);
    // &pagetoken=${this.data[0]};
    const urlVideos = `https://www.googleapis.com/youtube/v3/videos?part=statistics,snippet&id= ${this.uri.join(',')}&key=AIzaSyAEAgy-HxNHXVs5xfaUtiYLfsG1lbj0_1M`;
    const dataStatistics = await this.model.getOptionsWithStatistics(urlVideos);
    const renderClip = new ClipView();

    dataStatistics.items.forEach((element) => {
      const ClipId = element.id;
      const Clipname = element.snippet.title;
      const ClipChannelTitle = element.snippet.channelTitle;
      const ClipDate = element.snippet.publishedAt;
      const ClipViewCount = element.statistics.viewCount;
      const ClipDescription = element.snippet.description;
      const ClipScreen = element.snippet.thumbnails.medium.url;
      renderClip.renderVideo(ClipId, Clipname, ClipChannelTitle,
        ClipDate, ClipViewCount, ClipDescription, ClipScreen);
    });
  }

  navigationOfSlider() {
    // //////////////slider navigation
    document.getElementById('ClipList').addEventListener('click', () => {
      const allClips = document.querySelectorAll('.youtube-clip');
      document.getElementById('ClipList').style.setProperty('--n', allClips.length);
    });

    document.getElementById('ClipList').addEventListener('click', () => {
      const shift = this.slider.scrollLeft;
      let shiftClip = Math.round(shift / 600) + 1;
      if (shiftClip === 0) shiftClip = 1;
      document.getElementById('currentButton').style.setProperty('--n', shiftClip);
      document.getElementById('currentButton').innerText = shiftClip;
    });

    document.getElementById('nextButton').addEventListener('click', () => {
      const shift = this.slider.scrollLeft;
      let shiftClip = Math.round(shift / 600) + 1;
      if (shiftClip === 1) shiftClip = 1;
      this.slider.scrollLeft = this.slider.scrollLeft + 600;
      document.getElementById('currentButton').innerText = shiftClip;
      const allClips = document.querySelectorAll('.youtube-clip');
      document.getElementById('ClipList').style.setProperty('--n', allClips.length);
      document.getElementById('nextButton').style.setProperty('--n', shiftClip);
    });

    document.getElementById('prevButton').addEventListener('click', () => {
      const shift = this.slider.scrollLeft;
      let shiftClip = Math.round(shift / 600);
      if (shiftClip === 0) shiftClip = 1;
      if (this.slider.scrollLeft > 100) {
        this.slider.scrollLeft = this.slider.scrollLeft - 600;
      }
      document.getElementById('currentButton').innerText = shiftClip;
    });
  }

  slider() {
    this.slider = document.querySelector('.wrapper-clip-list');
    let isDown = false;
    let startX;
    let scrollLeft;

    this.slider.addEventListener('mousedown', (e) => {
      isDown = true;
      this.slider.classList.add('active');
      startX = e.pageX - this.slider.offsetLeft;
      scrollLeft = this.slider.scrollLeft;
    });
    this.slider.addEventListener('mouseleave', () => {
      isDown = false;
      this.slider.classList.remove('active');
    });
    this.slider.addEventListener('mouseup', () => {
      isDown = false;
      this.slider.classList.remove('active');
    });
    this.slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - this.slider.offsetLeft;
      const walk = (x - startX) * 3;
      this.slider.scrollLeft = scrollLeft - walk + 314;
    });
  }
}
