import BasicLayout from './controllers/basicLayoutController';
import ClipController from './controllers/clipController';

const app = new BasicLayout();
app.listenerofLoad();
function initClips(e) {
  const itemId = e.target.id;
  if (itemId === 'searchButton' || e.keyCode === 13) {
    if (document.getElementById('ClipList').childNodes.length > 0) {
      const element = document.getElementById('ClipList');
      while (element.firstChild) {
        element.removeChild(element.firstChild);
      }
    }
    const valueSearch = document.getElementById('searchLine').value;
    this.url = `https://www.googleapis.com/youtube/v3/search?&maxResults=15&q=${valueSearch}
           &key=AIzaSyBCTcAuPqI80DSuOhJ50hz12PLjfx4ktqY&part=snippet`;
    const clip = new ClipController(this.url);
    this.uri = clip.handlerOfOptions(this.url);
    clip.handle(this.uri);
    clip.slider();
    clip.navigationOfSlider();
  }
}

document.body.addEventListener('click', initClips);
document.body.addEventListener('keyup', initClips);
